import Vue from 'vue'
import * as getters from './getters'
import Vuex from 'vuex'
import action from './action'
import mutations from './mutations'
import state from './state'
Vue.use(Vuex)
const store = new Vuex.Store({
  state: state,
  // 存放方法
  mutations: mutations,
  getters: getters.default,
  actions: action

})
export default store



