import Vue from 'vue'
/**
 * 匹配电话号码
 * 15012341234 => 150****1234
 */
Vue.filter('phone', function (value) {
  if (!value) return ''
  let str = value
  str = str.toString().replace(/^(\d{3})(\d{4})(\d{4})/g, '$1****$3')
  return str
})

Vue.filter('IDCARDFILTE', function (value) {
  if (!value) return ''
  let str = value
  str = str.toString().replace(/^(.{1})(?:\w+)(.{1})$/, '$1********$2')
  return str
})

export default {
  init () {
    console.log('*** filter installed ***')
  }
}



