import state from './state'

const mutations = {
  declare (state, declare) {
    state.declare = declare
  },
  keepAlive (state, component) {
    !state.catchList.includes(component) &&
      state.catchList.push(component)
  },
  noKeepAlive (state) {
    state.catchList = []
  }
}
export default mutations



