// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

import router from './router'
// import * as dd from 'dingtalk-jsapi'
// UI框架
import Vant from 'vant'
import 'vant/lib/index.css'
import Vuex from 'vuex'
import store from './state'
import LicenseKeyboard from 'vue-license-keyboard'
import 'vue-license-keyboard/lib/vue-license-keyboard.css'
// import Setting from './setting'
import { EsitaMxins } from './common/EsitaMxins'
// 钉钉js 加密
import axios from 'axios' // HTTP请求
import {
  dateFormat
} from '@/common/util/dateTimeFilters'
import pipe from './common/pipe'
import VConsole from 'vconsole'
Vue.use(pipe)
Vue.use(LicenseKeyboard)
Vue.mixin(EsitaMxins)
Vue.prototype.$dateTimeFilters = dateFormat
// Vue.prototype.$imgUrl = 'http://ldrk.jk-kj.com'
Vue.use(Vant)
Vue.use(Vuex)

// let vconsole = new VConsole()
// Vue.use(dd)
// Vue.use(tabs)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: {
    App
  },
  template: '<App/>'
})



